

.App {
    h1 {
        font-size: 56px;
        font-weight: 700;
        line-height: 67px;
    }
    h2 {
        font-size: 48px;
        font-weight: 700;
        line-height: 58px;
    }
    h3 {
        font-size: 24px;
        font-weight: 700;
        line-height: 34px;
    }

    .text-lg {
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: 27px;
    }
    .text-med {
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
    }
    .text-sm {
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 21px;
    }
    .bold-text {
        font-weight: 600;
        color: var(--orange-color)
    }
    .title {
        text-align: center;
        margin-bottom: 40px;
        .sty-title {
            padding-bottom: 10px;
            position: relative;
            display: inline-flex;
            &:after {
                position: absolute;
                content: '';
                width: 80%;
                height: 2px;
                background-color: var(--sage-color);
                border-radius: 3px;
                top: 100%;
                left: 50%;
                transform: translateX(-50%);
            }
        }
    }
    /*Hero Section*/
    .hero-section-conten-box,.about-me-section,.my-skill-section,.my-port-content,.contace-me-content-box {
        max-width: 1200px;
        margin: 0 auto;
    }
    .hero-section {
        padding-top: 82px;
    }
    .hero-section-content {
        display: flex;
        gap: 20px;
        align-items: center;
        justify-content: flex-start;
        min-height: 70vh;
        padding: 50px 20px;
        .name-title {
            color: var(--sage-color);
            margin-bottom: 20px;
        }
    }
    .about-me-content {
        // height: uset;
        padding: 40px 20px;
        margin: 30px 0;
        .about-me-box {
            margin-bottom: 30px;
            .about-me-content-box {
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 90px;
                .ab-description {
                    max-width: 65%;
                    width: 100%;
                }
                .img-box {
                    max-width: 35%;
                    width: 100%;
                    img {
                        width: 100%;
                        height: auto;
                        border-radius: 20px;
                    }
                }
            }
        }
    }
    .my-skill-section {
        .my-skill-content {
            padding: 40px 20px;
            .my-skill-desc {
                text-align: center;
                margin:0 auto 80px;
                max-width: 650px;
                .text-hlg {
                    color: var(--orange-color);
                    font-weight: 600;
                }
            }
            .tech-stack-container {
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 50px;
                flex-wrap: wrap;
                margin: 0 auto;
                max-width: 650px;
                .tech-icon {
                    font-size: 60px;
                    &:hover{
                        color: var(--sage-color);
                    }
                }
            }
        }
    }
    .my-port-container-box {
        padding: 50px 20px;
        .port_container_box {
            .port_item {
                display: flex;
                gap: 30px;
                padding: 40px 0;
                .image_box {
                    max-width: 560px;
                    img {
                        width: 100%;
                        height: auto;
                        border-radius: 18px;
                        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
                    }
                }
                .port-title {
                    color: var(--orange-color);
                    font-weight: 600;
                    margin-bottom: 20px;
                }
                .port-desc {
                    margin-bottom: 15px;
                }
                .port_description_box {
                    padding: 20px;
                }
                .wrap_tech_stack {
                    display: flex;
                    gap: 15px;
                    margin-bottom: 30px;
                    flex-wrap: wrap;
                    .item {
                        display: inline-flex;
                        padding: 3px 10px;
                        border: 1px solid var(--sage-color);
                        border-radius: 3px;
                        color: var(--sage-color);
                        &:hover {
                            color: var(--orange-color);
                            border: 1px solid var(--orange-color);
                        }
                    }
                }
                .link-btn {
                    background: var(--sage-color);
                    padding: 5px 15px;
                    border-radius: 3px;
                    display: inline-flex;
                    color: var(--white-color);
                    text-decoration: none;
                    transition: background 0.5s ease-in-out;
                    &:hover {
                        background: var(--orange-color);
                    }
                }
            }
        }
    }
    .contact-me-section {
        background-color: var(--orange-color);
        color: var(--white-color);
        .wrab-content-box {
            padding: 50px 20px;
            .title {
                .sty-title {
                    &:after {
                        background-color: var(--white-color);
                    }
                }
            }
            .contact_bx {
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                gap: 15px;
                a {
                    text-decoration: none;
                    color: var(--white-color)
                }
                .contact_item {
                    display: inline-flex;
                    gap: 10px;
                    align-items: center;
                }
            }
        }
    }
    .my-resume-section {
        background-color: var(--sage-color);
        color: var(--white-color);
        .my-resume-content {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 40px 20px;
            .my-resume-wrap-content {
                text-align: center;
                .resume-btn {
                    text-decoration: none;
                    color: var(--white-color);
                    border: 1px solid var(--white-color);
                    border-radius: 3px;
                    padding: 5px 10px;
                }
                .resume-title {
                    font-weight: 600;
                    margin-bottom: 20px;
                }
            }
        }
    }
    .navbar {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 15px 20px;
        background: var(--sage-color);
        box-shadow: 0px 5.333px 80px rgba(0,0,0,0.1);
        // width: 100;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 4;
        color: var(--white-color);
        .text-hlg {
            font-weight: 700;
        }
        .nav__hamburger {
            width: 20px;
            height: 20px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            visibility: hidden;
            position: relative;
            .nav__hamburger_line {
                display: block;
                width: 100%;
                height: 2px;
                background-color: var(--white-color);
                border-radius: 3px;
                transition: all 0.3s ease-in-out;
            }
            &.active {
                visibility: visible;
            }
            &.open {
                .nav__hamburger_line {
                    &:nth-child(1), &:nth-child(3) {
                        position: absolute;
                        top: 50%;
                        // left: 0;
                    }
                    &:nth-child(1) {
                        transform: rotate(45deg) translateY(-50%);
                        left: -1px;
                    }
                    &:nth-child(3) {
                        transform: rotate(-45deg) translateY(-50%);
                    }
                    &:nth-child(2) {
                        visibility: hidden;
                        display: none;
                    }
                }
            }
        }
        .navber--items  {
            display: block;
            > ul {
                list-style: none;
                display: flex;
                align-items: flex-start;
                gap: 42.667px;
                text-decoration: none;
                > li {
                    text-decoration: none;
                    .navbar--content {
                        font-weight: 400;
                        cursor: pointer;
                        &.navbar--active-content {
                            color: var(--dark-grey-color)
                        }
                    }
                }
            }
            &.active {
                display: none;
                position: absolute;
                top: -100%;
                left: 0;
            }
            &.open {
                display: block;
                top: 100%;
                width: 100vw;
                background-color: var(--sage-color);
                // padding-bottom: 20px;
                > ul {
                    background-color: var(--sage-color);
                    padding: 0 0 20px;
                    margin: 0;
                    flex-direction: column;
                    gap: 20px;
                    align-items: center;
                }
            }
        }
        
    }
    #footer {
        background-color: var(--sage-color);
        color: var(--white-color);
        .wrap-text {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 10px;
            padding: 10px 20px;
        }   
    }
}

@media (max-width: 767px) {
    .App {
        .about-me-content {
            .about-me-box {
                .about-me-content-box {
                    flex-direction: column;
                    gap: 45px;
                    .ab-description {
                        max-width: 100%;
                    }
                    .img-box {
                        max-width: 100%;
                    }
                }
            }
        }  
        .my-port-container-box {
            .port_container_box {
                .port_item {
                    flex-direction: column;
                    gap: 15px;
                    padding: 20px 0;
                    .image_box {
                        max-width: 100%;
                        img {
                            border-radius: 12px;
                        }
                    }
                    .port_description_box {
                        padding: 20px 0;
                    }
                }
            }
        }  
    }
}