:root {
    /*Color Pallete*/
    --white-color: #fff;
    --black-color: #000;
    --beige-color: #FFFBF5;
    --sage-color: #94B49F;
    --dark-grey-color: #343434;
    --peach-color: #ECB390;
    --orange-color: #DF7861;
}

body {
    margin: 0;
    font-family: 'Montserrat', sans-serif;
    -webkit-font-smoothing: 'Montserat', sans-serif;
    -moz-osx-font-smoothing: 'Montserat', sans-serif;
    font-style: normal;
    background-color: var(--beige-color);
    color: var(--dark-grey-color);
    box-sizing: border-box;
}
h1,h2,h3,h4,h5,h6,p {
    margin: 0;
}